import cn from 'classnames';
import ReactDom from 'react-dom';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import SwiperCore, { Pagination, Navigation, Lazy } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { FiX, FiChevronLeft, FiChevronRight } from 'react-icons/fi';
import { motion, startAnimation, useMotionValue } from 'framer-motion';

import { LoadingIndicator } from '@mssgme/ui';
import { useUploads } from '../../hooks';

import styles from './LightBox.scss';

SwiperCore.use([Pagination, Navigation, Lazy]);

const animate = (from, to, transition) => {
    void startAnimation('', from, to, transition);

    return {
        stop: () => from.stop(),
    };
};

export default function LightBox({ images, activeIndex, onClose, onClosing }) {
    const { getMediaSizeUrl } = useUploads();
    const opacity = useMotionValue(0);
    const hasPagination = images.length > 1;
    const navigationPrevRef = React.useRef(null);
    const navigationNextRef = React.useRef(null);

    const handleClose = useCallback(() => {
        onClosing && onClosing();

        animate(opacity, 0, {
            type: 'spring',
            stiffness: 100,
            onComplete: onClose,
        });
    }, [onClose, onClosing]);

    useEffect(() => {
        const controls = animate(opacity, 1, {
            type: 'spring',
            stiffness: 100,
        });

        return controls.stop;
    }, []);

    return ReactDom.createPortal(
        <motion.div className={styles.root} style={{ opacity }}>
            <div className={styles.closeBtn} onClick={handleClose}>
                <FiX size="30" color="#f5f5f5" />
            </div>
            <div className={styles.content}>
                <Swiper
                    className={styles.swiper}
                    initialSlide={activeIndex}
                    spaceBetween={0}
                    slidesPerView={1}
                    allowTouchMove={hasPagination}
                    pagination={
                        hasPagination
                            ? {
                                  clickable: true,
                                  clickableClass: styles.clickablePagination,
                                  bulletActiveClass: styles.activeBullet,
                              }
                            : false
                    }
                    navigation={{
                        prevEl: navigationPrevRef.current ? navigationPrevRef.current : undefined,
                        nextEl: navigationNextRef.current ? navigationNextRef.current : undefined,
                    }}
                    speed={600}
                    watchOverflow={images.length > 1}
                    loop={images.length > 1}
                    lazy={{ loadPrevNext: true, preloaderClass: styles.preloader, loadedClass: styles.loaded }}
                    onInit={(swiper) => {
                        swiper.params.navigation.prevEl = navigationPrevRef.current;
                        swiper.params.navigation.nextEl = navigationNextRef.current;
                        swiper.navigation.update();
                    }}
                >
                    {images.map(({ url, annotation }, index) => (
                        <SwiperSlide key={index} className={styles.slide}>
                            {({ isActive }) => (
                                <div
                                    className={cn({
                                        [styles.slideContent]: true,
                                        [styles.active]: isActive,
                                    })}
                                >
                                    <div
                                        className={cn({
                                            [styles.imageBox]: true,
                                            [styles.noPagination]: images.length === 1,
                                        })}
                                    >
                                        <div className={styles.closableOverlay} onClick={handleClose} />
                                        <img
                                            data-src={getMediaSizeUrl(url)}
                                            className="swiper-lazy"
                                            alt={annotation || 'Image'}
                                        />
                                        <div className={styles.preloader}>
                                            <LoadingIndicator />
                                        </div>
                                    </div>
                                    {annotation && <div className={styles.annotation}>{annotation}</div>}
                                </div>
                            )}
                        </SwiperSlide>
                    ))}
                    {hasPagination && (
                        <>
                            <div className={cn(styles.navButton, styles.prev)} ref={navigationPrevRef}>
                                <FiChevronLeft size={24} />
                            </div>
                            <div className={cn(styles.navButton, styles.next)} ref={navigationNextRef}>
                                <FiChevronRight size={24} />
                            </div>
                        </>
                    )}
                </Swiper>
            </div>
        </motion.div>,
        document.querySelector('body')
    );
}

LightBox.propTypes = {
    images: PropTypes.array.isRequired,
    activeIndex: PropTypes.number.isRequired,
    onClose: PropTypes.func.isRequired,
    onClosing: PropTypes.func,
};
